import clsx from 'clsx';
import { Button, ButtonProps, ButtonVariant } from '..';
import {
  MediaTextBodyProps,
  MediaTextDescriptionProps,
  MediaTextHeadlineProps,
  MediaTextImageProps,
  MediaTextProps,
} from './media-text.interface';

const MediaText = ({ children, align = 'left' }: MediaTextProps) => {
  return (
    <div
      className={clsx('zep-flex zep-flex-col md:zep-flex-row zep-w-full', {
        'md:zep-flex-row-reverse': align === 'right',
      })}
      data-testid="zep-media-text"
    >
      {children}
    </div>
  );
};

const MediaTextImage = ({ src, alt, children, aspectRatio = '16:9' }: MediaTextImageProps) => {
  return (
    <div
      className={clsx('zep-relative zep-w-full zep-overflow-hidden zep-order-1 md:zep-order-2 zep-h-fit')}
      data-testid="zep-media-text-image"
    >
      {aspectRatio === '16:9' && <div className="zep-w-full zep-pb-[calc(9_*_100%_/_16)]"></div>}
      {aspectRatio === '9:16' && <div className="zep-w-full zep-pb-[calc(16_*_100%_/_9)]"></div>}
      {children ? (
        children
      ) : (
        <img src={src} alt={alt} className="zep-absolute zep-top-[0] zep-w-full zep-h-full zep-object-cover" />
      )}
    </div>
  );
};

const MediaTextBody = ({ children }: MediaTextBodyProps) => {
  return (
    <div className="zep-flex zep-items-center zep-order-2 md:zep-order-1 zep-w-full" data-testid="zep-media-text-body">
      <div className="zep-flex zep-flex-col zep-w-full zep-py-1.5 zep-px-1 zep-gap-1.5 sm:zep-py-2.5 sm:zep-px-1.5 md:zep-px-2.5 md:zep-py-[0] lg:zep-px-5">
        {children}
      </div>
    </div>
  );
};

const MediaTextHeadline = ({ children }: MediaTextHeadlineProps) => {
  return (
    <h2
      className="zep-typography-headlineMD sm:zep-typography-headlineXL zep-text-typography-dark-100"
      data-testid="zep-media-text-headline"
    >
      {children}
    </h2>
  );
};

const MediaTextDescription = ({ children }: MediaTextDescriptionProps) => {
  return (
    <div
      className="zep-flex zep-flex-col zep-gap-1 zep-typography-bodyText zep-text-typography-dark-100"
      data-testid="zep-media-text-description"
    >
      {children}
    </div>
  );
};

const MediaTextButton = ({
  children,
  label,
  onClick,
}: Pick<ButtonProps, 'children' | 'onClick' | 'icon' | 'iconPosition' | 'label'>) => {
  return (
    <Button
      variant={ButtonVariant.Secondary}
      onClick={onClick}
      className="md:zep-self-start"
      data-testid="zep-media-text-button"
      label={label}
    >
      {children}
    </Button>
  );
};

MediaText.Image = MediaTextImage;
MediaText.Body = MediaTextBody;
MediaText.Headline = MediaTextHeadline;
MediaText.Description = MediaTextDescription;
MediaText.Button = MediaTextButton;

export { MediaText };
